import React, { Fragment } from 'react';
import FormFieldRenderer from "../form-field-renderer";
import BaseFormWizard from "../form-wizard/base-form-wizard";
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';

export class BaseFormStepper extends BaseFormWizard {
    render() {
        return (
            <Fragment>
            {this.steps()}
            {this.screens()}
            </Fragment>
        )
    }
    steps() {
        const steps = this.fields.map(field=>field?.meta?.displayName?field.meta.displayName:field.name);
        return (
            <Stepper activeStep={this.state.activeIndex} alternativeLabel>
                {steps.map((label) => (
                <Step key={label}>
                    <StepLabel>{label}</StepLabel>
                </Step>
                ))}
            </Stepper>
        )
    }
    screens(): JSX.Element {
        const field = this.fields.find((_f,i)=>i===this.state.activeIndex);
        const form = this.context.form[field?.name?field.name:'default'];
        const sync = () => false;
        return (
            <Fragment>
                {field && <FormFieldRenderer
                    {...field}
                    key={field.name}
                    section={field.name} 
                    form={form} 
                    sync={sync}/>}
                {this.footer()}
            </Fragment>
        )
    }

    footer() {
        if (this.state.activeIndex<this.fields.length-1) {
            return (
                <div className="row text-center m24">
                    <div className="col">
                        <button className="btn btn-default" onClick={this.next.bind(this)}>Next</button>
                    </div>
                </div>
            )
        }
        return (<Fragment></Fragment>)
    }

    next(e: React.SyntheticEvent) {
        if (this.state.activeIndex<this.fields.length-1) {
            this.setActiveIndex(this.state.activeIndex+1);
        }
        e.preventDefault();
    }

}